@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');
.navbar {
    height: 6.5rem;
    position: absolute;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
}

.navbar--container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    padding: 0 3rem;
    margin-top: 1rem;
    /* background-color: aqua; */
}

.navbar--container > h1 {
    font-family: 'Great Vibes', cursive;
    letter-spacing: 4px;
    color: var(--primary-2);
    font-size: 2.5rem;
    user-select: none;
}

.navLink--container {
    margin-top: 5rem;
}

.drawer > .MuiBackdrop-root {
    background: rgba(33, 33, 33, 0.15) !important;
    backdrop-filter: blur(20px) !important;
}
.navbar-right{
    /* background-color: bisque; */
    width: 6vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media (max-width:1100px) {
    .navbar--container{
        padding: 0 2rem;
    }
    .navbar--container > h1{
        font-size: 2.2rem;
    }
    .nav-menu{
        font-size: 2.2rem;
        margin-top: -1.5rem;
    }
    .navbar-right{
        flex-direction: column-reverse;
        padding-top: 35px;
    }
    .navbar{
        height: 7rem;
        margin-top: -15px;
        /* background-color: aquamarine; */
    }
}

@media (max-width:800px) {
    .navLink--container {
        margin-top: 3rem;
    }
}


@media (max-width:600px) {
    .nav-menu{
        color: var(--primary-2);
    }
    .navbar--container > h1{
        font-size: 2rem;
    }
    .nav-menu:hover{
        color: var(--primary-2);
    }
    .MuiDrawer-paper{
      border-radius: 0 !important;
    }
}

@media (max-width:400px) {
    .navbar{
        width: 100vw !important;
        /* background-color: aqua; */
        padding: 0;
        margin: 0;
    }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
    .navbar {
        height: 5rem;
    }

    .navbar--container {
        padding: 0rem 1rem;
        margin-top: 1rem 0;
    }
    .navbar--container>h1 {
        font-size: 1.5rem;
    }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 
  480px) and (-webkit-device-pixel-ratio: 2) and (device-aspect-ratio: 2/3) {
    .navbar {
        height: 5rem;
    }
    .navbar--container {
        padding: 0 1rem;
        margin-top: 0rem;
    }
    .navbar--container > h1 {
        font-size: 1.3rem;
    }
}